import tools from '@/common/tools.js'
import axios from 'axios'

export default {
  name: 'aftersale',
  data: function() {
    return {
      liData: [],
      loadingShow: false,
      noDataShow: false,
      totalPages: 0,
      busy: true,
      timer: null,
      nowPage: 1
    }
  },
  activated() {
    this._resetData()
    this._getSaleData()
  },
  methods: {
    _resetData() {
      this.liData = []
      this.loadingShow = false
      this.noDataShow = false
      this.totalPages = 0
      this.busy = true
      this.timer = null
      this.nowPage = 1
    },
    gotoRefundDetails(id) {
      console.log(id)
      this.$router.push({'path': 'refunddetails', query: {recId: id}})
    },
    loadMore() {
      console.log('重复加载', this.busy)
      this.loadingShow = true
      this.busy = true
      // 官方示例中延迟了1秒，防止滚动条滚动时的频繁请求数据
      this.timer = setTimeout(() => {
        // 这里请求接口去拿数据，实际应该是调用一个请求数据的方法
        this.nowPage++
        this._getSaleData()
      }, 1000)
    },
    _getSaleData() {
      let url = '/Mobile/Order/return_goods_list.html'
      axios.get(url, {
        params: {
          p: this.nowPage
        }
      })
        .then((res) => {
          console.log(res)
          if (res.data.status == 1) {
            this.totalPages = res.data.totalPages
            this.liData = [...this.liData, ...res.data.goods_list]
            console.log('当前页：', this.nowPage, this.totalPages)
            if (this.nowPage >= this.totalPages) {
              this.busy = true
              this.loadingShow = false
              this.noDataShow = true
            } else {
              this.busy = false
            }
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    }
  }
}



